<template>
  <div class="card min-vh-50">
    <div class="card-header pb-0">
      <div class="d-flex justify-content-between">
        <div class="d-flex gap-4 align-items-center">
          <h4>Admins</h4>
          <div v-if="tableProgress.loading" class="spinner-border spinner-border" role="status"></div>
        </div>
        <button-component
          color="success"
          size="sm"
          variant="outline"
          :isLoading="tableProgress.loading"
          :isDisabled="tableProgress.loading"
          @click="toggleAddModal()"
        >
          Add Admin
        </button-component>
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table v-if="!tableProgress.loading" class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Admin</th>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2">name / phone</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Status</th>
              <th class="text-secondary opacity-7"></th>
              <th class="text-secondary opacity-7"></th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="admin in adminsList" :key="admin.id">
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <!-- :src="'http://testingnbackend.somee.com' + admin.imageUrl" -->
                    <img src="../../assets/img/team-2.jpg" class="avatar avatar-sm me-3" alt="user1" />
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{ admin.userName }}</h6>
                    <p class="text-xs text-secondary mb-0">{{ admin.email }}</p>
                  </div>
                </div>
              </td>
              <td>
                <p class="text-xs font-weight-bold mb-0">{{ admin.name }}</p>
                <p class="text-xs text-secondary mb-0">{{ admin.phone }}</p>
              </td>
              <td class="align-middle text-center text-sm">
                <span
                  class="badge badge-sm"
                  :class="{
                    'bg-gradient-secondary': admin.status === userStatus.Disabled,
                    'bg-gradient-success': admin.status === userStatus.Verified,
                  }"
                >
                  {{ admin.status === userStatus.Verified ? 'Active' : 'Disabled' }}
                </span>
              </td>
              <td class="align-middle text-center">
                <a @click="toggleEditModal(admin.id)" class="btn btn-link text-dark">
                  <i class="fas fa-pencil-alt text-dark me-2" aria-hidden="true"></i>Edit
                </a>
              </td>
              <td class="align-middle">
                <a @click="toggleDeleteModal(admin.id)" class="btn btn-link text-danger text-gradient">
                  <i class="far fa-trash-alt me-2" aria-hidden="true"></i>Delete
                </a>
              </td>
              <td class="align-middle">
                <a @click="toggleResetPasswordModal(admin.id)" class="btn btn-link text-danger text-gradient">
                  Reset Password
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <AddAdminModal v-if="modals.addAdmin" />
      <EditAdminModal v-if="modals.editAdmin" />
      <DeleteAdminModal v-if="modals.deleteAdmin" />
      <ResetPasswordAdmin v-if="modals.resetPasswordAdmin" />
    </div>
  </div>
</template>

<script>
  import { userStatuses } from '@/constants/user-status';
  import ButtonComponent from '@/components/Button.vue';
  import { defineAsyncComponent } from 'vue';

  export default {
    name: 'Admins-table',
    props: { adminsList: Object },
    data() {
      return {
        userStatus: userStatuses,
      };
    },
    components: {
      ButtonComponent,
      AddAdminModal: defineAsyncComponent(() => import('./admins-actions/add-admin')),
      DeleteAdminModal: defineAsyncComponent(() => import('./admins-actions/delete-admin')),
      EditAdminModal: defineAsyncComponent(() => import('./admins-actions/edit-admin')),
      ResetPasswordAdmin: defineAsyncComponent(() => import('./admins-actions/reset-password-admin')),
    },
    methods: {
      toggleAddModal() {
        this.$store.commit('AdminsStore/TOGGLE_ADD_ADMIN_MODAL');
      },

      toggleDeleteModal(adminId) {
        this.$store.commit('AdminsStore/SET_ADMIN_ID', adminId);
        this.$store.commit('AdminsStore/TOGGLE_DELETE_ADMIN_MODAL');
      },

      toggleEditModal(adminId) {
        this.$store.commit('AdminsStore/SET_ADMIN_ID', adminId);
        this.$store.commit('AdminsStore/TOGGLE_EDIT_ADMIN_MODAL');
      },

      toggleResetPasswordModal(adminId) {
        this.$store.commit('AdminsStore/SET_ADMIN_ID', adminId);
        this.$store.commit('AdminsStore/TOGGLE_RESET_PASSWORD_ADMIN_MODAL');
      },
    },

    computed: {
      tableProgress() {
        return this.$store.state.AdminsStore.progress.adminList;
      },
      modals() {
        return this.$store.state.AdminsStore.modals;
      },
    },
  };
</script>
