<template>
  <div class="py-4 container-fluid">
    <div class="row">
      <div class="col-lg-12">
        <adminTable :adminsList="getAdminsResponse.data" />
        <PaginationComponent
          v-if="getAdminsResponse.totalCount"
          class="mt-4"
          :pageCount="Math.ceil(getAdminsResponse.totalCount / getAdminsResponse.pageSize)"
          :perPage="pagination.pageSize"
          :value="currentPage"
          :align="'center'"
          @input="handlePageChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
  import adminTable from './adminsTable';
  import PaginationComponent from '@/components/Pagination';
  export default {
    name: 'admins-page',
    components: { adminTable, PaginationComponent },

    data() {
      return {
        pagination: { pageSize: 10, pageNumber: 1, currentPage: 1 },
      };
    },

    mounted() {
      this.$store.dispatch('AdminsStore/loadAdminList', {
        pageNumber: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
      });
    },

    methods: {
      handlePageChange(newPage) {
        this.currentPage = newPage;
        this.$store.dispatch('AdminsStore/loadAdminList', {
          pageNumber: this.pagination.pageNumber * this.currentPage,
          pageSize: this.pagination.pageSize,
        });
      },
    },
    computed: {
      getAdminsResponse() {
        return this.$store.state.AdminsStore.adminsList;
      },
    },
  };
</script>
